body {
  background-color: #2fc1d4;
}

.breed-select {
  width: 250px;
  height: 35px;
  background: none;
  border: 1px solid #ccc;
  font-size: 18px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.breed {
  background-color: #b5dce6;
  outline: 1px solid #ff0;
  gap: 32px;
  margin: 15px;
  display: flex;
  box-shadow: 0 2px 18px rgba(0, 0, 0, .3);
}

.breed-image {
  width: 400px;
}

.loader {
  width: 32px;
  height: 32px;
  color: #e9d1c9;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 48px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 48px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

.loader {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
}

